@import url('https://fonts.googleapis.com/css2?family=My+Soul&family=PT+Sans+Narrow&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'PT Sans Narrow', sans-serif;
  background: #0059ff;
}

#quote-box {
   background: #fff;
   width: 40rem;
   margin: 10rem auto;
   padding: 2rem;
   border-radius: 5px;
}

#text {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  quotes:"\201C''\201D''\2018''\2019"
}

#text:before {
  content: open-quote;
  display: inline;
  height: 0;
  line-height: 0;
  left: -10px;
  position: relative;
  top: 30px;
  color: #ccc;
  font-size: 3em;
}

#author {
  display: flex;
  justify-content: flex-end;
  font-size: 1.4rem;
}

#buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
}

.social-media {
  display: flex;
}

.social-media > a {
  margin-right: .4rem;
  background: #0059ff;
  padding: .3rem .5rem;
  cursor: pointer;
  border-radius: 3px;
}

.social-media > a img {
  width: 1.2rem;
}

.social-media > a:hover {
    opacity: 0.9;
}

#new-quote {
  background: #0059ff;
  border: none;
  color: #fff;
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
}

#new-quote:hover {
  opacity: 0.9;
}